import Vue from 'vue'
import VueRouter from 'vue-router'
import index from '../views/index.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: index
  },
  {
    path: '/about',
    name: 'about',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '../views/AboutView.vue')
  },
  {
    path: '/notice',
    name: 'notice',
    component: () => import('@/views/notice/index-01.vue')
  },
  {
    path: '/detail',
    name: 'detail',
    component: () => import('@/views/notice/notice-detail.vue')
  },
  {
    path: '/recruitment-detail',
    name: 'recruitment-detail',
    component: () => import('@/views/recruitment-detail/index-01.vue')
  },
  {
    path: '/admissionTicket',
    name: 'admissionTicket',
    component: () => import('@/views/admissionTicket/index.vue')
  },
  {
    path: '/interviewIndex',
    name: 'admissionTicket',
    component: () => import('@/views/admissionTicket/interviewIndex.vue')
  },
  {
    path: '/my',
    name: 'my',
    component: () => import('@/views/my/index-01.vue')
  },
  {
    path: '/form',
    name: 'form',
    component: () => import('@/views/form/index.vue')
  }
]

const router = new VueRouter({
  mode: 'hash',
  base: process.env.BASE_URL,
  routes
})

export default router
